<style scoped="scoped" lang="less">
	.addagentform{
        padding: 40px !important;
    }
    /deep/.ivu-form-item-label{
        text-align: left;
    }
    /deep/.ivu-input,/deep/.ivu-select-single,/deep/.ivu-select-selection,/deep/.ivu-select-selected-value{
        width: 260px;
    }
</style>
<template>
    <Form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="84" class="addagentform">
        <FormItem label="  ">
            <span>3515259:00()100</span>
        </FormItem>
		<FormItem label=" ">
            <Input v-model="count"  disabled="disabled" />
        </FormItem>
        <FormItem label="">
            <Input v-model="balance"  disabled="disabled" />
        </FormItem>
        <FormItem label=""  prop="cashOutAmount">
            <Input v-model="formCustom.cashOutAmount" placeholder="" />
        </FormItem>
        <FormItem label="">
            <Select v-model="alipayMethod">
                <Option value="alipay"></Option>
            </Select>
        </FormItem>
		<FormItem label="" prop="alipayAccount">
            <Input v-model="formCustom.alipayAccount" placeholder="" />
        </FormItem>
		<FormItem label="" prop="alipayName">
            <Input v-model="formCustom.alipayName" placeholder="" />
        </FormItem>
		<FormItem label="" prop="cashOutPassword">
            <Input v-model="formCustom.cashOutPassword" placeholder="" />
        </FormItem>
        <FormItem>
            <Button type="primary" @click="handleSubmit('formCustom')"></Button>
        </FormItem>
    </Form>
</template>
<script>
    export default {
        data () {
            const validateAlipayAccount = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else {
                    callback();
                }
            };
            const validateAlipayName = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else {
                    callback();
                }
            };
            const validateCashOutAmount = (rule, value, callback) => {
                if (!(/(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/.test(value))) {
                    callback(new Error(','));
                }else if(Number(value) === 0){
                    callback(new Error('0'));
                }  else {
                    callback();
                }
            };
            const validateCashOutPassword = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                }  else {
                    callback();
                }
            };
            return {
                balance:'',
                count:'',       
                alipayMethod:'alipay',
                money:'',
                formCustom: {
					cashOutAmount:'',
                    alipayAccount:'',
                    alipayName:'',
                    cashOutPassword:'',
                },
                ruleCustom: {
                    alipayAccount: [
                        { validator:validateAlipayAccount , trigger: 'blur' }
                    ],
                    cashOutAmount: [
                        { validator:validateCashOutAmount , trigger: 'blur' }
                    ],
                    alipayName: [
                        { validator:validateAlipayName , trigger: 'blur' }
                    ],
                    cashOutPassword: [
                        { validator:validateCashOutPassword , trigger: 'blur' }
                    ],
                }
            }
		},
		methods:{
            moneyFun(){
                if(!(/(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/.test(this.cashOutAmount))){
                    this.$Message.error('')
                }else{
                    this.money = this.cashOutAmount*100;
                }
            },
            // 
            getInitData(){
                this.$fetch('/cashOut/queryCountMonth',{
                })
				.then((data)=>{
                    if(data.code==200){
                        this.balance = data.balance;
                        this.count = data.count;
                    }else{
                        this.$$Message.error('')
                    }
				})
            },
            handleSubmit (name) {
				var that = this;
                this.$refs[name].validate((valid) => {
                    if (valid) {
						console.log(this.formCustom)
						let params = this.formCustom;
						this.$fetch('/cashOut/withdraw',params).then((data)=>{
                            console.log(data)
                            if(data.code == '500'){
                                that.$Message.error(',!');
                            }else if(data.code == '200'){
                                that.$Message.success('!');
                                that.$router.push({path: '/WithdrawalRecord', replace: true});
                            }else if(data.code == '501'){
                                that.$Message.error('0!');
                            }else if(data.code == '502'){
                                that.$Message.error('!');
                            }else if(data.code == '505'){
                                that.$Message.error('!');
                            }else{
								// 0. 1. 2. 3. 4.
								that.$Message.error(data.message);
							}
						});
					} else {
						that.$Message.error('');
					}
				})
            },
            // 
			withDraw(){
				var that = this;
				var params ={};
				params={
					cashOutAmount:this.cashOutAmount,
					alipayAccount:this.alipayAccount,
					alipayName:this.alipayName,
				}
				that.$post('/cashOut/withdraw',params).then((data)=>{
					console.log(data)
					if(data.code == '500'){
						that.$Message.error(',!');
					}else if(data.code == '200'){
						that.$Message.success('');
					}
				})
			}
        },
        created(){
            this.getInitData()
        }
    }
</script>



