// 
let handleFormatError = function(file) {
	this.$Notice.warning({
		title: '',
	});
}
// 
let handleMaxSize = function(file) {
	this.$Notice.warning({
		title: '',
		desc: 'File  ' + file.name + ' is too large, no more than 2M.'
	});
}
// 
let handleBeforeUpload = function() {
	const check = this.uploadList.length < 11;
	if (!check) {
		this.$Notice.warning({
			title: '10.'
		});
	}
	return check;
}

// 
const comFilters = {
	//  
	clearNoNum:function (val){ 
		
		// val = val.replace(/[^\d]/g,"");  //  
		// conso.log(val)
		val = val.replace(/[^\d.]/g,"");  //.  
		val = val.replace(/^[+]{0,1}(\d+)$/g,"");  		
		val = val.replace(/\.{2,}/g,"."); //.   
		val = val.replace(".","$#$").replace(/\./g,"").replace("$#$","."); 
		val = val.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//  
		if(val.indexOf(".")< 0 && val !=""){// 0102 
			val= parseFloat(val); 
		} 
		return val
	} 
}

