<style scoped="scoped" lang="less">
.formBox{
    width: 400px;
    padding-top: 40px;
}
</style>
<template>
    <Form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="80" class="formBox">
        <FormItem label="" >
            <Input type="text" v-model="formCustom.phoneNum" disabled="disabled" />
        </FormItem>
        <FormItem label="">
            <Input type="password" v-model="formCustom.cashOutPwd"  disabled="disabled"/>
        </FormItem>
        <FormItem label="" prop="passwd">
            <Input type="password" v-model="formCustom.passwd"  placeholder="5-16"/>
        </FormItem>
        <FormItem label="" prop="passwdCheck">
            <Input type="password" v-model="formCustom.passwdCheck" placeholder=""/>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="handleSubmit('formCustom')"></Button>
        </FormItem>
    </Form>
</template>
<script>
    export default {
        data () {
            const validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                }else if(value.length<5||value.length>16){
                    callback(new Error('5-16'));
                }else {
                    if (this.formCustom.passwdCheck !== '') {
                        // 
                        this.$refs.formCustom.validateField('passwdCheck');
                    }
                    callback();
                }
            };
            const validatePassCheck = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if (value !== this.formCustom.passwd) {
                    callback(new Error(''));
                } else {
                    callback();
                }
            };
            
            
            return {
                formCustom: {
                    phoneNum:this.$store.state.loginAccount,
                    cashOutPwd:sessionStorage.getItem('cashOutPwd'),
                    passwd: '',
                    passwdCheck: '',
                },
                ruleCustom: {
                    passwd: [
                        { validator: validatePass, trigger: 'blur' }
                    ],
                    passwdCheck: [
                        { validator: validatePassCheck, trigger: 'blur' }
                    ],
                }
            }
        },
        methods: {
            handleSubmit (name) {
                var that = this;
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        // this.$Message.success('Success!');
                        var params = {};
                        params = {
                            password:this.formCustom.cashOutPwd,//
                            cashOutPwd:'',//
                            changePwd:this.formCustom.passwd,//
                        };
                        that.$fetch('/user/changePwd', params).then((data)=>{
                            console.log(data)
                            if(data.code == '500'){
                                that.$Message.error(',!');
                            }else if(data.code == '200'){
                                that.$Message.success('!');
                                that.$router.push({path: '/login', replace: true});
                                sessionStorage.clear();
                            }else if(data.code == '501'){
                                that.$Message.error(',!');
                            }else if(data.code == '502'){
                                that.$Message.error(',!');
                            }else if(data.code == '503'){
                                that.$Message.error(',!');
                            }
                        })
                    } else {
                        this.$Message.error('!');
                    }
                })
            },
            handleReset (name) {
                this.$refs[name].resetFields();
            }
        }
    }
</script>



